    background: linear-gradient(transparent, #ad8079);
    border-radius: 12px;
}

::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(#D7DBDD, #ad8079);
}

@media (max-width: 576px) {
  ::-webkit-scrollbar {
    width: 4px;
}

::-webkit-scrollbar-thumb {
    background: linear-gradient(transparent, #ad8079);
    border-radius: 6px;
}

::-webkit-scrollbar-thumb:hover {
    background: linear-gradient(#D7DBDD, #ad8079);
}
}

